.MessageList mb-5 {
  border-right: 1px solid var(--twitter-background);
  flex: 0.25;
  /* min-width: 250px; */
  margin-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.sidebar_twitterIcon {
  color: var(--twitter-color);
  font-size: 30px !important;
  margin-left: 20px;
